.quote {
  height: 33.594vw;
  &__container {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 6.3vw;
    height: 100%;
  }
}

.quote-content {
  display: flex;
  flex-direction: column;
  gap: 1.563vw;
  max-width: 60.625vw;
  img {
    display: block;
    width: 6.406vw;
    height: 5.313vw;
  }
}

.quote-text {
  font-family: var(--font-family-Jost);
  font-size: 2.083vw;
  line-height: 3.125vw;
  text-transform: uppercase;
}

.quote-animation {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;

  &__column {
    padding: 0 0.781vw;
    flex-shrink: 0;
    animation: item-animate 20s ease-out infinite;
  }

  &__item {
    width: 17.76vw;
    height: 100%;
    border: 1px solid #c9c9c9;
    border-radius: 10px;
  }
}

@keyframes item-animate {
  0% {
    transform: translateZ(0);
  }

  50% {
    transform: translate3d(-100%, 0, 0);
  }

  100% {
    transform: translateZ(0);
  }
}

@include tablet {
  .quote {
    height: 90vw;
    &__container {
      padding-top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .quote-content {
    gap: 3.922vw;
    max-width: 91.563vw;
    img {
      width: 16.078vw;
      height: 13.333vw;
    }
  }

  .quote-text {
    font-size: 3.922vw;
    line-height: 5.882vw;
  }

  .quote-animation {
    &__column {
      padding: 0 2vw;
    }

    &__item {
      width: 50vw;
      border-radius: 1.307vw;
    }
  }
}
